import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';

import { AdminComponent } from './admin.component';
import { HomeComponent } from './pages/home/home.component';
import { ProjectsComponent } from './pages/projects/projects.component';
import { ProjectSingleComponent } from './pages/project-single/project-single.component';
import { SkillsComponent } from './pages/skills/skills.component';
import { SkillSingleComponent } from './pages/skill-single/skill-single.component';

const routes: Routes = [
  { path: '', component: AdminComponent,
    children: [
      { path: '', component: HomeComponent },
      { path: 'projetos', component: ProjectsComponent},
      { path: 'projetos/:id', component: ProjectSingleComponent},
      { path: 'skills', component: SkillsComponent},
      { path: 'skills/:id', component: SkillSingleComponent}
    ]
  }
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class AdminRoutingModule { }
