import { Component, OnInit } from '@angular/core';
import { ApiBackendService } from '../../services/api-backend.service';
import { DateFormatPipe } from '../../../pipe/date-format.pipe';

export class Project {
	id: number
	title: string
	company: string
	description: string
	date_start: Date
	date_end: Date
}

@Component({
	selector: 'app-projects',
	templateUrl: './projects.component.html',
	styleUrls: ['./projects.component.scss']
})
export class ProjectsComponent implements OnInit {
	componentRoute: string = "projects";
	projects: Project | null = null;

	constructor(private apiBackend: ApiBackendService, private datePipe: DateFormatPipe) { }

	ngOnInit() {
		this.apiBackend.get('projects', {}).subscribe(
			projects => { 
				this.projects = projects
				console.log("this.projects: ", this.projects) 
			},
			err => { console.error('ERRO PORRA', err)}
		);


	}

	parseDate(date: Date){
		return this.datePipe.transform(date, "MM/yyyy");
	}

}
